let mainBannerSwiper = new Swiper(".js-swiper__banner", {
  speed: 500,
  direction: "horizontal",
  breakpoints: {
    768: {
      direction: "vertical",
    },
  },
  // autoplay: {
  //   delay: 4000,
  //   disableOnInteraction: false,
  // },
});

if (document.querySelector(".l-banner__video")) {
  const video = document.querySelector(".l-banner__video");
  const progressContainer = document.querySelector(".video-progress-container");
  const progressBar = progressContainer.querySelector(".progress-bar");
  const marker = progressContainer.querySelector(".marker");

  video.addEventListener("timeupdate", updateProgressBar);

  function updateProgressBar() {
    const timeIndicator = progressContainer.querySelector(".time-indicator");
    const currentTime = video.currentTime;
    const duration = video.duration;
    const remainingTime = duration - currentTime;

    const percent = (video.currentTime / video.duration) * 100;
    progressBar.style.width = `${percent}%`;
    marker.style.left = `${percent}%`;

    timeIndicator.textContent = `${formatTime(remainingTime)}`;
  }

  // Формат таймера на баннере
  function formatTime(time) {
    const minutes = Math.floor(time / 60);
    const seconds = Math.floor(time % 60);
    return `${minutes.toString().padStart(2, "0")}:${seconds
      .toString()
      .padStart(2, "0")}`;
  }
}

// Orbit пагинация
const blockTitles = document.querySelectorAll(".swiper-slide[data-title]");
// макс число
const maxVisibleElements = 5;
// Определяем список элементов, которые будут видны в пагинации
const visibleElements = Array.from(blockTitles).slice(0, maxVisibleElements);

const pagination = document.querySelector(".l-banner_navigation");

function getScreenWidth() {
  if (window.innerWidth) {
    return window.innerWidth;
  } else if (document.documentElement && document.documentElement.clientWidth) {
    return document.documentElement.clientWidth;
  } else if (document.body && document.body.clientWidth) {
    return document.body.clientWidth;
  } else if (screen.width) {
    return screen.width;
  }

  return null;
}
let radius = 250; // Радиус круга

const screenWidth = getScreenWidth();

if (screenWidth && screenWidth <= 768) {
  radius = 150;
}
visibleElements.forEach(function (title, index) {
  const angle = (90 / blockTitles.length) * index;

  const paginationItem = document.createElement("div");
  const paginationItemChild = document.createElement("div");
  paginationItem.appendChild(paginationItemChild);

  paginationItem.classList.add("l-pagination__name");
  paginationItemChild.textContent = title.getAttribute("data-title");

  // Вычисляем позицию элемента
  const posX = radius * Math.cos((angle * Math.PI) / 180);
  const posY = radius * Math.sin((angle * Math.PI) / 180);

  paginationItem.style.transform = `translate(calc(50% - ${posX}px), calc(50% - ${posY}px)) rotate(${angle}deg)`;

  paginationItem.addEventListener("click", function () {
    const activeItem = document.querySelector(".l-pagination__name.active");
    if (activeItem) {
      activeItem.classList.remove("active");
    }

    // Добавляем класс "active" выбранному элементу пагинации
    paginationItem.classList.add("active");

    // Перемещаем активный элемент в центр
    pagination.style.transform = ` rotate(${-angle}deg)`;

    // Переключаем активный элемент
    // setActivePaginationItem(index);

    mainBannerSwiper.slideTo(index);
  });

  pagination.appendChild(paginationItem);
});

