// modal
const btns = document.querySelectorAll(".l-btn__modal");
const modal = document.querySelector(".l-modal__component");
const closeBtn = document.querySelector(".l-modal__component .close");
const modalBg = document.querySelector(".l-modal__component .bg");
const body = document.body;
let scrollPosition = 0;

if (document.querySelector(".l-modal__component")) {
  btns.forEach((btn) => {
    btn.addEventListener("click", () => {
      scrollPosition = window.pageYOffset || document.documentElement.scrollTop;
      // Открываем модальное окно
      modal.classList.add("active");
      body.classList.add("unscroll");
      window.scrollTo(0, 0);
    });
  });

  closeBtn.addEventListener("click", () => {
    modal.classList.remove("active");
    body.classList.remove("unscroll");
    window.scrollTo(0, scrollPosition);
  });
  modalBg.addEventListener("click", () => {
    modal.classList.remove("active");
    body.classList.remove("unscroll");
    window.scrollTo(0, scrollPosition);
  });
}

// phone validator
const phoneInput = document.querySelector(".phone");

if (document.querySelector(".phone")) {
  phoneInput.addEventListener("input", formatPhoneNumber);
}

function formatPhoneNumber() {
  const phoneNumber = phoneInput.value.replace(/\D/g, ""); // Удаляем все, кроме цифр
  const formattedPhoneNumber = phoneNumber.replace(
    /^(\d{1})?(\d{0,3})?(\d{0,3})?(\d{0,2})?(\d{0,2})?/,
    (match, p1, p2, p3, p4, p5) => {
      let formattedNumber = "+";
      if (p1) {
        formattedNumber += p1 + "(";
      }
      if (p2) {
        formattedNumber += p2 + ")-";
      }
      if (p3) {
        formattedNumber += p3 + "-";
      }
      if (p4) {
        formattedNumber += p4 + "-";
      }
      if (p5) {
        formattedNumber += p5;
      }
      return formattedNumber;
    }
  );

  phoneInput.value = formattedPhoneNumber;
}

