// accordion
document.querySelectorAll(".accordion").forEach((accordion) => {
  let activeIndex = null;

  const accordionToggle = accordion.querySelectorAll(".submenu-toggle");
  const accordionList = accordion.querySelectorAll(".accordion > li");

  accordion.querySelectorAll(".submenu").forEach((submenu) => {
    submenu.style.height = submenu.clientHeight + "px";
    submenu.parentNode.classList.add("close");
  });

  accordionToggle.forEach((toggleBtn, index) => {
    toggleBtn.addEventListener("click", (e) => {
      if (activeIndex !== null && activeIndex !== index) {
        accordionList[activeIndex].classList.add("close");
      }
      e.target.parentNode.classList.toggle("close");

      activeIndex = index;
    });
  });
});

//feedback инпут появляется при нажатии на чекбокс "нет"
if (document.querySelectorAll(".hidden-input")) {
  const checkboxes = document.querySelectorAll(".checkbox-js");

  checkboxes.forEach(function (checkbox) {
    checkbox.addEventListener("change", function () {
      const hiddenInput =
        this.closest(".l-input").querySelector(".hidden-input");
      const checkboxeYes = this.closest(".l-input").querySelector(
        'input[data-checkbox="yes"]'
      );
      const checkboxeNo = this.closest(".l-input").querySelector(
        'input[data-checkbox="no"]'
      );

      if (this.value == "no") {
        hiddenInput.classList.add("enable-input");
        checkboxeYes.checked = false;
      } else {
        hiddenInput.classList.remove("enable-input");
        checkboxeNo.checked = false;
      }

      if (checkboxeNo.checked == false) {
        hiddenInput.classList.remove("enable-input");
      }
    });
  });
}

