// обращение
const block = document.querySelector(".l-content-obrashenie");

function handleResize() {
  const screenWidth = window.innerWidth;
  if (screenWidth <= 1440 && screenWidth >= 1014) {
    const scale = screenWidth / 1440; // Вычисляем масштаб
    const translateX = -(1440 - screenWidth) / 2; // Вычисляем смещение по горизонтали
    block.style.transform = `scale(${scale}) translateX(${translateX}px)`;
  } else {
    block.style.transform = "none";
  }
}

if (document.querySelector(".l-content-obrashenie")) {
  window.addEventListener("resize", handleResize);
}


